<template>
  <div>
      <b-button v-b-modal.modal-1 @click="modalShow = !modalShow">Create Campaign</b-button>
      <b-modal id="modal-1" title="Campaign" v-model="modalShow">
          <div class="container" >
            <div class="row">
              <div>
                <form
                  class="text-center border border-warning p-5"
                  style="margin-top:50px;padding-top:50px !important;"
                  @submit="onSubmit"
                >		
            
            <h1> New Campaign </h1>
                  <input
                    type="text"
                    id="Title"
                    class="form-control mb-5"
                    placeholder="Title"
                    v-model="form.Title"
              required
                  />
                  <input
                    type="text"
                    id="Message"
                    class="form-control mb-5"
                    v-model="form.Message"
                    placeholder="Description"
              required
                  />

                  <b-form-file
                        v-model="form.image"
                        :state="Boolean(form.image)"
                        placeholder="Choose a file or drop it here..."
                        drop-placeholder="Drop file here..."
                    ></b-form-file>
                    <div class="mt-3">Selected file: {{ form.image ? form.image.name : '' }}</div>
                  
                  <center>
                  <b-button type="submit" variant="primary">Create Campaign</b-button>
                  </center>
                </form>
              </div>
            </div>
          </div>
      </b-modal>
  </div>
</template>

<style>

</style>

<script>
import { api } from '../helpers/helpers';
import VueJwtDecode from "vue-jwt-decode";

export default {
  name: 'new_campaign',
  props: {
    method: { type: Function },
  },
  data() {
    return {
      form: {
          Title: '',
          Message: '',
          image: null,
          PostedBy: null
      },
      modalShow: false
    };
  },
    methods: {
        async onSubmit(event) {
            event.preventDefault()

            let formData = new FormData();
            formData.append('Title',this.form.Title)
            formData.append('Message',this.form.Message)
            formData.append('PostedBy',this.form.PostedBy)
            formData.append('image', this.form.image)

            let res = await api.createcamp(formData);
            this.form = {
                Title: '',
                Message: '',
                image: null,
                PostedBy: null
            }
            let camps = await api.getcampaigns();
            
            this.$emit('new-campaign', camps);
            this.modalShow = false
            
        },
        onReset(event) {
            event.preventDefault()
            this.form = {
                Title: '',
                Message: '',
                image: null
            }
        }
  },
  async mounted() {
    this.camps = await api.getcampaigns();
	  this.ads = await api.getads();
    let token = localStorage.getItem("jwt");
    let decoded = VueJwtDecode.decode(token);
    this.form.PostedBy = decoded.fname;
  }
};
</script>