import Vue from "vue";
import VueRouter from "vue-router";
import Vuelidate from "vuelidate";
import Campaigns from "../views/Campaigns.vue";
import login from "../views/login.vue";
import register from "../views/register.vue";
import Usercampaign from "../views/Usercampaign.vue";
Vue.use(VueRouter);
const routes = [
  {
    path: "/Campaigns",
    name: "Campaigns",
    component: Campaigns,
	 meta: {
      disallowAuthed: true
    }
	
  },
  {
    path: "/",
    name: "Campaigns",
    component: () => import("../views/Campaigns.vue")
  },
  {
    path: "/register",
    name: "register",
    component: () => import("../views/register.vue")
  },
   {
    path: "/login",
    name: "login",
    component: () => import("../views/login.vue")
  },
   {
    path: "/Usercampaign",
    name: "Usercampaign",
    component: () => import("../views/Usercampaign.vue"),
	 meta: {
      requiresAuth: true
    }
  },
  {
    path: "/manage",
    name: "Manage",
    component: () => import("../views/ManageCampaigns.vue"),
	 meta: {
      requiresAuth: true
    }
  },
  {
    path: "/new_campaign",
    name: "new_campaign",
    component: () => import("../views/NewCampaign.vue"),
    meta: {
        requiresAuth: true
      }
  }
];
const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes
});
router.beforeEach((to, from, next) => {
  if (to.matched.some(record => record.meta.requiresAuth)) {
    if (localStorage.getItem("jwt") == null) {
      next({
        path: "/"
      });
    } else {
      next();
    }
  } else {
    next();
  }
});
export default router;