<template>
  <div class="container" style="height:100vh">
    <div class="row">
	
      <div class="col-lg-6 offset-lg-3 col-sm-10 offset-sm-1">
	
        <form
          class="text-center border border-warning p-5"
          style="margin-top:50px;padding-top:100px !important;"
          @submit.prevent="registerUser"
        >
		
		<p style="float: right; "><router-link to="/"
              >Home</router-link> </p>
			  <img style="height:20%; width:20%" src="Dogoodlogo.ico"  >
		 <h1 style="color:Black"> Register </h1>
          <input
            type="text"
            id="fname"
            class="form-control mb-5"
            placeholder="First Name"
            v-model="register.fname"
            required
          />
		  <input
            type="text"
            id="lname"
            class="form-control mb-5"
            placeholder="Last Name"
            v-model="register.lname"
            required
          />
          <input
            type="email"
            id="email"
            class="form-control mb-5"
            placeholder="Email"
            v-model="register.email"
            required
          />
          <!-- Password -->
		  
		  
       <input
            type="password"
            id="password"
            class="form-control mb-5"
            placeholder="Password"
            v-model="register.password"
			 required
          /> 
		
		   
          <p style="color:Black">
            Already have an account??<router-link to="/login"
              >click here</router-link
            >
            <!-- Sign in button -->
            <center>
              <button class="btn btn-warning btn-block w-75 my-4" type="submit">
                Register
              </button>
            </center>
          </p>

        </form>
      </div>
    </div>
  </div>
</template>
<script>
import swal from "sweetalert";
export default {
  data() {
    return {
      register: {
        fname: "",
		lname: "",
        email: "",
        password: ""
      }
    };
  },
    methods: {
 async registerUser() {
      try {
        let response = await this.$http.post("/user/register", this.register);
        console.log(response);
        let token = response.data.token;
        if (token) {
          localStorage.setItem("jwt", token);
          this.$router.push("/login");
          swal("Success", "Registration Was successful", "success");
        } else {
          swal("Error", "Something Went Wrong", "error");
        }
      } catch (err) {
        let error = err.response;
        if (error.status == 409) {
          swal("Error", error.data.message, "error");
        } else {
          swal("Error", error.data.err.message, "error");
        }
      }
    }
   }
};
</script>