<template>
  <div class="container" style="height:100vh">
    <div class="row">
      <div class="col-lg-6 offset-lg-3 col-sm-10 offset-sm-1">
        <form
          class="text-center border border-warning p-5"
          style="margin-top:50px;padding-top:50px !important;"
          @submit.prevent="loginUser"
        >
		
		<img style="height:20%; width:20%" src="Dogoodlogo.ico"  >
		
		
		<p style="float: right"><router-link to="/"
              >Home</router-link> </p>
		
		
		<h1 style="color:Black"> Login </h1>
          <input
            type="email"
            id="email"
            class="form-control mb-5"
            placeholder="Email"
            v-model="login.email"
			required
          />
          <!-- Password -->
          <input
            type="password"
            id="password"
            class="form-control mb-5"
            placeholder="Password"
            v-model="login.password"
			required
          />
          <p style="color:Black">
            Dont have an account??<router-link to="/register"
              >click here</router-link
            >
          </p>
          <!-- Sign in button -->
          <center>
            <button class="btn btn-warning btn-block w-75 my-4" type="submit">
              Sign in
            </button>
          </center>
        </form>
      </div>
    </div>
  </div>
</template>
<script>
import swal from "sweetalert";
export default {
  components: {
  },
  data() {
    return {
      login: {
        email: "",
        password: ""
      }
    };
  },
  methods: {
    async loginUser() {
      try {
        let response = await this.$http.post("/user/login", this.login);
        console.log(response)
        let token = response.data.token;
        localStorage.setItem("jwt", token);
		
        if (token) {
          swal("Success", "Login Successful", "Error");
          this.$router.push("/Usercampaign");
        }
      } catch (err) {
          swal("Error", "Something Went Wrong", "error");
        console.log(err.response);
      }
    }
  }
};
</script>