<template>
  <nav class="navbar navbar-expand navbar-brand bgcolor">
        <a @click="usercampaignpage">
        	<img  src="Dogoodlogo.ico"  style="width: 100px"/>
        </a>
	   <h1  class="Appname" >  DOGOOD </H1>
        <div
            class="collapse navbar-collapse justify-content-end"
            id="navbarNav"
        >
            <ul class="navbar-nav">
                <li class="nav-item" v-if="!user">
                    <a class="nav-link" @click="registerpage"> Register</a>
                </li>

                <li class="nav-item" v-if="!user">
                    <a class="nav-link" @click="loginpage"> Login</a>
                </li>

                <li class="nav-item" v-if="user">
                    <a class="nav-link" @click="usercampaignpage"> Home</a>
                </li>

                <li class="nav-item" v-if="user">
                    <a class="nav-link" @click="managepage" v-if="user.role === 'admin'"> Manage Campaign</a>
                </li>

                 <li class="nav-item" v-if="user">
                    <a class="nav-link" @click="logUserOut"> Logout</a>
                </li>
                
            </ul>
      </div>
    </nav>
</template>

<script>
import VueJwtDecode from "vue-jwt-decode";

export default {
    name: 'Navbar',
    data() {
        return {
            user: null
        };
    },
    methods: {
        getUserDetails() {
            let token = localStorage.getItem("jwt");
            let decoded = VueJwtDecode.decode(token);
            console.log(decoded);
            this.user = decoded;
            },
        async logUserOut() {
            localStorage.removeItem("jwt");
            this.$router.push("/");
        },
		async registerpage() {
		this.$router.push("/register");
		},
		async managepage() {
		this.$router.push("/manage");
		},
		async loginpage() {
		this.$router.push("/login");
		},
		async usercampaignpage() {
		this.$router.push("/Usercampaign");
		}
		
    },
    created() {
        this.getUserDetails();
    }

}
</script>

<style>
.Appname
{
Color: Black;
Font-family : Kunstler Script;
}
nav .navbar-nav li a
{
color: Black;
font-weight: Bold;
}
</style>