import axios from 'axios';
import Vue from 'vue';
import VueFlashMessage from 'vue-flash-message';
import 'vue-flash-message/dist/vue-flash-message.min.css';

Vue.use(VueFlashMessage, {
  messageOptions: {
    timeout: 3000,
    pauseOnInteract: true
  }
});

const vm = new Vue();
const baseURL = 'http://localhost:3000/user/campaigns/';
const baseadURL = 'http://localhost:3000/user/ads/';

const handleError = fn => (...params) =>
  fn(...params).catch(error => {
    vm.flash(`${error.response.status}: ${error.response.statusText}`, 'error');
  });
  
  export const api = {
  getcamp: handleError(async id => {
    const res = await axios.get(baseURL + id);
    return res.data;
  }),
  getcampaigns: handleError(async () => {
    const res = await axios.get(baseURL);
    return res.data;
  }),
  getAllCampaigns: handleError(async () => {
    const res = await axios.get(baseURL+'all');
    return res.data;
  }),
  getcampaigns: handleError(async () => {
    const res = await axios.get(baseURL);
    return res.data;
  }),
  deletecamp: handleError(async id => {
    const res = await axios.delete(baseURL + id);
    return res.data;
  }),
  createcamp: handleError(async payload => {
    let config = {
      header : {
       'Content-Type' : 'multipart/form-data'
     }
    }
    const res = await axios.post(baseURL, payload, config);
    return res.data;
  }),
  updatecamp: handleError(async payload => {
    const res = await axios.put(baseURL + payload._id, payload);
    return res.data;
  }),
  
   getAdv: handleError(async id => {
    const res = await axios.get(baseadURL + id);
    return res.data;
  }),
  getads: handleError(async () => {
    const res = await axios.get(baseadURL);
    return res.data;
  }),
  deleteAdv: handleError(async id => {
    const res = await axios.delete(baseadURL + id);
    return res.data;
  }),
  createAdv: handleError(async payload => {
    const res = await axios.post(baseadURL, payload);
    return res.data;
  }),
  updateAdv: handleError(async payload => {
    const res = await axios.put(baseadURL + payload._id, payload);
    return res.data;
  }),
  
  
};