<template>
  <div >
    <navbar />
    <div style="background-color:#fefce7">
      <h1 style="padding-right:1%; color: Black; text-align: right"> Welcome {{ user.fname }} </h1>
    </div>

    <div style="padding-left:2%">
     
	  </br>
      
    </div>

    <div class="row">
      <b-col col="8" md="9" >
        <div class="row">
		<h1 style="color:#6b9fcf; font-size:40; text-align: center">Campaigns</h1>
		<div style="padding-left: 3%">
		 <new-campaign @new-campaign="campaignCreated"/>
		 </div>
          <div v-for="(Campaign, i) in camps" :key="i" style="color: #fff; width: 50%; padding: 3%; padding-bottom: 10%;">
		 
            <h3 style="color: Black;">{{Campaign.Topic}}</h3>
            <img :src="'http://localhost:3000/'+Campaign.image" />
            <p style="font-style: italic; color: Black;" >Posted By: {{ Campaign.PostedBy }} on {{ (new Date(Campaign.PostedDate)).toLocaleString() }} </p>      
            <h3 style="color: Black;">{{Campaign.Message}}</h3>
			
            <ShareNetwork
              network="facebook"
              :url="location"
              :title="Campaign.Topic"
              style="padding: 10px"
            >
              <b-icon icon="facebook" aria-hidden="true"></b-icon>
            </ShareNetwork>
            <ShareNetwork
                network="twitter"
                :url="location"
                :title="Campaign.Topic"
                style="padding: 10px"
              >
              <b-icon icon="twitter" aria-hidden="true"></b-icon>
            </ShareNetwork>
            <ShareNetwork
                network="whatsapp"
                :url="location"
                :title="Campaign.Topic"
                style="padding: 10px"
              >
              <b-icon icon="chat-text" aria-hidden="true"></b-icon>
            </ShareNetwork>
			
          </div>
        </div>
      </b-col>

      <b-col col="4" md="3" >
	  <div style="background-color: #6b9fcf">
	  <h1 style="color:white; text-align: center">Advertisements</h1>
	  <div style="text-align: right; padding-right: 5%">
	   <create-adv />
	   </div>
	</div>
          <div v-for="(Adv, i) in ads" :key="i" style="padding: 5%; width: 100%; background-color: #6b9fcf; color:white">
            <img :src="'http://localhost:3000/'+Adv.imageUrl" />
          </div>
          <!-- <table id="Adv"  style="border:1px solid white; width: 100%"   >
            <tr v-for="(Adv, i) in ads" :key="i">
              <td>
                <div >
                  <img fluid :src="'http://localhost:3000/'+Adv.imageUrl" />
                </div>
              </td>
            </tr>
          </table> -->
		  
      </b-col>
    </div>

  </div>
</template>

<style>
.row {
  display: flex;
  flex-wrap: wrap;
  padding: 0 4px;
}
</style>


<script>

import { api } from '../helpers/helpers';
import VueJwtDecode from "vue-jwt-decode";
import NewCampaign from "./NewCampaign.vue"
import CreateAdv from "./CreateAdv.vue"
import Navbar from '../components/Navbar.vue';

export default {
  name: 'camps',
  components: {
    NewCampaign,
    CreateAdv,
    Navbar
  },
  data() {
    return {
      camps: [],
	    user: {},
	    ads: [],
      location: location.href
    };
  },
  methods: {
    async onDestroy(id) {
      const sure = window.confirm('Are you sure?');
      if (!sure) return;
      await api.deletecamp(id);
      this.flash('Campaign deleted sucessfully!', 'success');
      const newcamps = this.camps.filter(Campaign => Campaign._id !== id);
      this.camps = newcamps;
    },
	  getUserDetails() {
      let token = localStorage.getItem("jwt");
      let decoded = VueJwtDecode.decode(token);
      this.user = decoded;
    },
	
	 logUserOut() {
      localStorage.removeItem("jwt");
      this.$router.push("/");
    },
    campaignCreated(value){
      this.camps = value
    }
  },
  async mounted() {
     let res = await api.getcampaigns();
     this.camps = res
      console.log(res)
	   this.getUserDetails();
	  this.ads = await api.getads();
    console.log(this.ads)
  }
};
</script>